import { React, useState } from "react";
import ListContainer from "./Body/ListContainer";
import SelectShop from "./Body/SelectShop";
import Footer from "./Footer/Footer";
import Header from "./Header/Header";
import state from "./State/State";

function App() {
  const [initialShops, initialSetShops] = useState(
    window.localStorage.setItem("initialShops", JSON.stringify(state))
  );

  const [shops, setShops] = useState(
    JSON.parse(window.localStorage.getItem("shops")) ||
      JSON.parse(window.localStorage.getItem("initialShops"))
  );

  //const [shops, setShops] = useState(shops);

  return (
    <>
      <Header />
      <SelectShop shops={shops} setShops={setShops} />
      <ListContainer shops={shops} setShops={setShops} />
      <Footer />
    </>
  );
}

export default App;
