import { React, useState } from "react";

const ItemInput = ({ setShops, shop, index }) => {
  const [item, setItem] = useState("");

  const handleSubmit = (e, id) => {
    e.preventDefault();
    if (!item) {
      return;
    } else {
      setShops((shops) => {
        return shops.reduce((acc, shop) => {
          if (shop.id === id) {
            //     setList([...list, { item, id: uuidv4() }]); //npm package that generates random ID's
            // window.localStorage.setItem(
            //   "shops",
            //   JSON.stringify([...acc, { ...shop, list: [...shop.list, item] }])
            // );
            return [...acc, { ...shop, list: [...shop.list, item] }];
          }
          // window.localStorage.setItem("shops", JSON.stringify([...acc, shop]));
          return [...acc, shop];
        }, []);
      });
      setItem("");
      setShops((shops) => {
        return shops.reduce((acc, shop) => {
          if (shop.id === id) {
            return [...acc, { ...shop, listLength: shop.list.length }];
          }
          return [...acc, shop];
        }, []);
      });
    }
  };

  return (
    <div>
      <form
        className="input-container"
        onClick={(e) => handleSubmit(e, shop.id, index)}
      >
        <input
          className="list-input"
          type="text"
          placeholder="Add to list"
          onChange={(e) => setItem(e.target.value)}
          value={item}
        />
        <button className="add-btn" type="submit">
          Add
        </button>
      </form>
    </div>
  );
};

export default ItemInput;
